@import url(https://fonts.googleapis.com/css2?family=Tourney:wght@100&display=swap);
.App {
  text-align: center;
  
}
.nav-section{
  height: 5vh;
  width: 96vw;
  text-align: right;
}
.cards-wrapper{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));
}
.memory-card{
    display: flex;
    justify-content: center;
    margin: 1rem;
    border: 1px solid transparent;
    border-radius: 10px;
    transition: all 0.15s linear;
}
.cust-btn{
    background-color: black;
    color: rgba(240, 46, 170);
    border: 1px solid rgba(240, 46, 170);
    cursor: pointer;
    padding: .5rem;
    font-size: 1.5rem;
    font-family: 'Tourney', cursive;
    
}
.cust-btn:hover{
    background-color: rgba(240, 46, 170);
    color: black;
}

@media (min-width: 1921px){
    .cards-wrapper{
        grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    }
}
@media (min-width: 786px){
    .card{
        display: block;
    }
}
.scoreboard{
    display: flex;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    font-size: 1.5rem;
}
h4{
    margin: 1vh;
}
.header{
    margin-left: 0;
    font-size: 1.75rem;
    width: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}
h2{
    margin: 0 !important;
}
.menu-button{
    position: absolute;
    top:2.25rem;
    right: 1rem;
    height: 30px;
    width: 30px;
}
@media(max-width:767px){
    .menu-button{
        right: 0;
    }
    .menu-icon{
        right: 10px;
    }
}

.menu-container{
    position: fixed;
    right: 0;
    top: 0;
    height: 100vh;
    width: 33vw;
    background-color: black;
    border: 2px solid rgba(240, 46, 170); 
    z-index: 99;
    padding-top: 1rem;
    text-align: center;
}
.menu-header{
    width: 100%;
    display: flex;
    justify-content: center;
}
a{
    color: rgba(240, 46, 170) !important;
    text-decoration: none !important;
}
svg{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
@media(max-width: 767px){
    .menu-container{
        width: 66vw;
    }
    svg{
        right: 30px;
    }
}

img{
    height: 90%;
    width: 100%;
    border: 2px solid white;
    border-radius: 15px;
}
.image-wrapper{
    height: 75%;
    width: 95%;
}

.character-wrapper{
    height: 35vh;
    width: 100%;
    padding: 1rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgba(240, 46, 170);
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 10px;
    transition: all 0.15s linear;
    opacity: .8;
}
.character-wrapper:hover{
    opacity: 1;
    box-shadow: rgba(240, 46, 170, 0.4) -5px 5px, rgba(240, 46, 170, 0.3) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px, rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
}
.char-head{
    font-family: 'Tourney', cursive !important;
}
@media (max-width: 768px){
    .character-wrapper{
        height: 40vh;
        width: 65vw;
    }
    .character-wrapper:hover{
        opacity: .8;
        box-shadow: none;
    }
}
.memory-sort-container{
    background-color: black;
    color: rgba(240, 46, 170);
}
.memory-sort-header{
    font-size: 1.75rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.grid-sort{
    display: grid;
    grid-template-columns: 75%;
    justify-content: center;
    text-align: center;
    padding-left: 0;
    grid-gap: 10px;
    gap: 10px;
    margin-bottom: 1vh;
}
.off{
    display: none;
}
.grid-cell-sort{
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.sort-image-wrapper img{
    height: 90%;
    width: 40%;
    border: 1px solid black;
    border-radius: 15px;
}
.sort-image-wrapper{
    margin-top: 10px;
    text-align: center;
    height: 90%;
    width: 50%;
}
.sort-char-header{
    margin-top: 0;
    font-size: 1.1rem;
    text-align: center;
}
.sort-character-wrapper{
    height: 12vh;
    padding: 1rem;
    font-family:'Times New Roman', Times, serif;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgba(240, 46, 170);
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 10px;
    transition: all 0.15s linear;
    opacity: .8;
    cursor: grabbing;
    margin-bottom: 10px;
}

@media(min-width:950px){
    .sort-image-wrapper{
        text-align: center;
        height: 90%;
        width: 35%;
    }
    .sort-character-wrapper{
        width: 100%;
        font-family: 'Tourney' , cursive;
        
    }
    .sort-character-wrapper:hover{
    opacity: 1;
    box-shadow: rgba(240, 46, 170, 0.4) -5px 5px, rgba(240, 46, 170, 0.3) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px, rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
    }
}
.win{
    display: flex;
    width: 50vw;
}
.lost{
    width: 50vw;
    display: block;
}

@media(max-width:900px){
    .win{
        width: 100%;
    }
    .lost{
        width: 100%;
    }
}

.modal-btn{
    background-color: black;
    color: rgba(240, 46, 170);
    border: 1px solid rgba(240, 46, 170);
    cursor: pointer;
    padding: .5rem;
    font-size: 1rem;
    font-family: 'Tourney', cursive;
    
}
.modal-btn:hover{
    background-color: rgba(240, 46, 170);
    color: black;
}

body {
  margin: 2rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: rgba(240, 46, 170)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

